button {
	transition: all .3s cubic-bezier(0, 0, .58, 1);
	-webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
    &:active,
    &:focus,
    &:hover {
        outline: 0!important
    }
}

.btn-green {
    color: #fff;
    background-color: #28a745 !important;
    border-color: #155724 !important;
    &:hover {
     color: #fff;   
    }
}