.jumbotron {
    padding: 8rem 2rem !important;
    background: #0071e2;
    border-radius: 0;
    background: url('../images/hero-bg.svg') no-repeat center;
    background-size: cover;
}

.hero-cap {
    .hero-cap__inner {
        margin-top: 76px;
        color: #fff;
        p {
            margin: 0;
            line-height: 4px;
            font-weight: 100;
            font-size: 1.6em;
        }
        #typed {
            font-size: 1.2em;
            font-weight: 100;
            line-height: 0;
        }
        strong {
            font-weight: 700;
        }
    }
}

.form-signin {
    max-width: 440px;
    background: #fff;
    border-radius: 5px;
    margin-top: 40px;
    label {
        font-size: .75em;
        font-weight: 500;
    }
    .is-input {
        position: relative;
        .invalid-tooltip {
            width: auto !important;
            font-size: .75rem;
        }
    }
}