/* GLOBAL STYLES
-------------------------------------------------- */
/* Padding below the footer and lighter body text */

body {
    color: #5a5a5a;
  }
  
  
  /* CUSTOMIZE THE CAROUSEL
  -------------------------------------------------- */
  
  /* Carousel base class */
  .carousel {
    margin-bottom: 4rem;
  }
  /* Since positioning the image, we need to help out the caption */
  .carousel-caption {
    bottom: 3rem;
    z-index: 10;
  }
  
  /* Declare heights because of positioning of img element */
  .carousel-item {
    height: 32rem;
    background-color: #777;
  }
  .carousel-item > img {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    height: 32rem;
  }
  
  
  /* MARKETING CONTENT
  -------------------------------------------------- */
  
  /* Center align the text within the three columns below the carousel */
  .marketing .col-lg-4 {
    margin-bottom: 1.5rem;
    text-align: center;
  }
  .marketing h2 {
    font-weight: 400;
  }
  .marketing .col-lg-4 p {
    margin-right: .75rem;
    margin-left: .75rem;
  }
  
  
  /* epayazs
  ------------------------- */
  
  .epayaz-divider {
    margin: 5rem 0; /* Space out the Bootstrap <hr> more */
  }
  
  /* Thin out the marketing headings */
  .epayaz-heading {
    font-weight: 300;
    line-height: 1;
    letter-spacing: -.05rem;
    margin-bottom: 70px;
    position: relative;
    &:after {
      content: '';
      width: 50px;
      height: 2px;
      position: absolute;
      bottom: -20px;
      left: 0;
      background: #0038fd;
    }
  }


  /* Why Epayaz
  -------------------------------------------------- */
  .whyEpayaz {
    position: relative;
    padding: 90px 0;
    background: url('../images/contract.jpg') no-repeat center;
    background-size: cover;
    margin: 100px 0;
    &:after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.78);
    }
    .contents,
    .epayaz-heading-sub  {
      position: relative;
      z-index: 10;
      color: #fff;
    }
    .epayaz-heading-sub {
      font-size: 2.5em;
      font-weight: 100;
    }
    .contents {
      font-weight: 100;
      font-size: 1.2em;
    }
    .text-muted {
      color: #007bff!important;
      // font-weight: 700;
    }
  }

  .epayaz-image {
    margin-top: 100px;
  }

    /* services
  -------------------------------------------------- */
  .icons {
    img {
      width: 20%;
    }
  }

  footer{
    margin-top: 100px;
  }

  .footer {
    color: #eaeaea;
    a {
      color:  #eaeaea;
    }
  }

  
  /* RESPONSIVE CSS
  -------------------------------------------------- */
  
  @media (min-width: 40em) {
    /* Bump up size of carousel content */
    .carousel-caption p {
      margin-bottom: 1.25rem;
      font-size: 1.25rem;
      line-height: 1.4;
    }
  
    .epayaz-heading {
      font-size: 50px;
    }
  }
  
